import androidx.compose.runtime.CompositionLocalProvider
import com.varabyte.kobweb.core.AppGlobalsLocal
import com.varabyte.kobweb.navigation.RoutePrefix
import com.varabyte.kobweb.navigation.Router
import kotlin.Unit
import kotlinx.browser.document
import kotlinx.browser.window
import org.jetbrains.compose.web.renderComposable

public fun main(): Unit {
    RoutePrefix.set("")
    val router = Router()
    com.varabyte.kobweb.core.init.initKobweb(router) { ctx ->
        ctx.router.register("/") { com.bibek.webproject.pages.HomePage() }

    }
    com.varabyte.kobweb.silk.init.initSilkHook = { ctx ->
        ctx.theme.registerComponentStyle(com.bibek.webproject.styles.AboutImageStyle)
        ctx.theme.registerComponentStyle(com.bibek.webproject.styles.AboutTextStyle)
        ctx.theme.registerComponentStyle(com.bibek.webproject.styles.BackToTopButtonStyle)
        ctx.theme.registerComponentStyle(com.bibek.webproject.styles.NavigationItemStyle)
        ctx.theme.registerComponentStyle(com.bibek.webproject.styles.LogoStyle)
        ctx.theme.registerComponentStyle(com.bibek.webproject.styles.SocialLinkStyle)
        ctx.theme.registerComponentStyle(com.bibek.webproject.styles.MainButtonStyle)
        ctx.theme.registerComponentStyle(com.bibek.webproject.styles.MainImageStyle)
        ctx.theme.registerComponentStyle(com.bibek.webproject.styles.PortfolioSectionStyle)
        ctx.theme.registerComponentStyle(com.bibek.webproject.styles.PortfolioArrowIconStyle)
        ctx.theme.registerComponentStyle(com.bibek.webproject.styles.ServiceCardStyle)
        com.bibek.webproject.updateTheme(ctx)
    }

    router.navigateTo(window.location.href.removePrefix(window.location.origin))

    // For SEO, we may bake the contents of a page in at build time. However, we will overwrite them
    // the first time we render this page with their composable, dynamic versions. Think of this as
    // poor man's hydration :)
    // See also: https://en.wikipedia.org/wiki/Hydration_(web_development)
    val root = document.getElementById("root")!!
    while (root.firstChild != null) {
        root.removeChild(root.firstChild!!)
    }

    renderComposable(rootElementId = "root") {
        CompositionLocalProvider(
            AppGlobalsLocal provides mapOf("title" to "Bibekananda Nayak")
        ) { com.bibek.webproject.MyApp {
                com.varabyte.kobweb.silk.defer.renderWithDeferred { router.renderActivePage() }
            }
        }
    }
}
